const utils = {
  /**
 * @description 
 * @param {number} value
 * @param {number} [max=5]
 * @param {number} [msg='0.01']
 * @return {string}
*/
  validateMoney: (value, max = 5, msg = '0.01') => {
    const patten1 = '(^([0-9]?\\.)[0-9]{1,2}$)' // 0.01
    const patten2 = `(^[1-9][0-9]{0,${max}}$)` // 123456 max
    const patten3 = `(^[1-9][0-9]{0,${max}}?\\.[0-9]{1,2}$)` // 999999.99 ,max,max+2
    const patten = patten1 + '|' + patten2 + '|' + patten3
    if (!new RegExp(patten).test(value)) {
      return msg
    } else {
      return 'ok'
    }
  },

  /**
 * @description 
 * @param {number} value
 * @return {string}
 */
  getDate: (value) => {
    const date = new Date()
    date.setDate(date.getDate() + value)
    const y = date.getFullYear()
    let m = date.getMonth() + 1
    m = m < 10 ? '0' + m : m
    let d = date.getDate()
    d = d < 10 ? '0' + d : d
    return y + '-' + m + '-' + d
  },

}

export {
  utils
}